
.scroll {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: auto;
}
.scroll .loadTip {
  position: relative;
  height: 0.8rem;
}
.scroll .tipCon {
  position: absolute;
  width: 6.4rem;
  height: 0.8rem;
  top: 0;
  left: 50%;
  margin-left: -3.2rem;
}
.scroll .tipCon .loadT1,
.scroll .tipCon .loadT2,
.scroll .tipCon .loadT3 {
  position: absolute;
  width: 0.2rem;
  height: 0.2rem;
  top: 0.3rem;
  background-color: #60a2ff;
  border-radius: 50%;
}
.scroll .tipCon .loadT1 {
  left: 2.5rem;
  -webkit-animation: loadAni linear infinite 0.9s;
}
.scroll .tipCon .loadT2 {
  left: 3.1rem;
  -webkit-animation: loadAni linear infinite 0.9s 0.3s;
}
.scroll .tipCon .loadT3 {
  right: 2.5rem;
  -webkit-animation: loadAni linear infinite 0.9s 0.6s;
}
@-webkit-keyframes loadAni {
0% {
    -webkit-transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
}
}



