
.goods {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0.9rem;
  overflow: auto;
  padding-top: 0.8rem;
}
.goods .searchRow {
  padding: 0.15rem 0.3rem;
  background-color: #31bfcf;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  box-sizing: border-box;
}
.goods .searchRow input {
  display: block;
  border: 1px solid #fff;
  background: none;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  height: 0.5rem;
  border-radius: 0.3rem;
  padding-left: 0.6rem;
}
.goods .searchRow input::-webkit-input-placeholder {
  color: #fff;
}
.goods .searchRow input:-ms-input-placeholder {
  color: #fff;
}
.goods .searchRow input::placeholder {
  color: #fff;
}
.goods .searchRow .fa-search {
  color: #fff;
  position: absolute;
  top: 0.26rem;
  left: 0.48rem;
}
.goods .goodsList {
  padding: 0 0.2rem;
}
.goods .goodsList li {
  border-bottom: 1px solid #eee;
  height: 1rem;
  line-height: 1rem;
  position: relative;
  padding-left: 1rem;
}
.goods .goodsList li .goodsName {
  font-size: 0.4rem;
  position: absolute;
  height: 1rem;
  line-height: 1rem;
  width: 1rem;
  top: 0;
  left: 0;
}
.goods .goodsList li .goodsNum {
  position: absolute;
  height: 1rem;
  line-height: 1rem;
  width: 1.5rem;
  top: 0;
  right: 0;
  color: #f00;
  font-size: 0.24rem;
}
.goods .goodsList li .goodsType,
.goods .goodsList li .goodsColor {
  color: #999;
  font-size: 0.24rem;
}
.goods .goodsList li:last-child {
  border: none;
}



