
.numBtn {
  display: inline-block;
  height: 0.4rem;
  width: 1.5rem;
  vertical-align: middle;
  position: relative;
  text-align: center;
  line-height: 0.4rem;
}
.numBtn .subNumBtn,
.numBtn .addNum {
  display: inline-block;
  vertical-align: bottom;
  width: 0.3rem;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: center;
  font-size: 0.4rem;
  font-weight: bold;
  border: 0.05rem solid #00a0dc;
  border-radius: 50%;
  position: absolute;
}
.numBtn .subNumBtn {
  color: #00a0dc;
  background-color: #fff;
  left: 0;
}
.numBtn .addNum {
  color: #fff;
  background-color: #00a0dc;
  right: 0;
}
.numBtn .num {
  color: #ccc;
  position: absolute;
  width: 0.5rem;
  border: 1px solid #eee;
  border-radius: 0.1rem;
  height: 0.4rem;
  text-align: center;
  top: 0;
  left: 0.5rem;
  box-sizing: border-box;
}



