
.orderDetail {
  padding: 0.8rem 0 3.5rem 0;
}
.orderDetail .orderMes {
  padding: 0.2rem 0.3rem;
  border-bottom: 0.2rem solid #f3f3f3;
}
.orderDetail .orderMes .orderTime {
  font-size: 0.24rem;
  line-height: 0.5rem;
  border-bottom: 1px solid #eee;
  color: #999;
}
.orderDetail .orderMes .orderTime .deliveryState {
  float: right;
  color: #f00;
}
.orderDetail .orderMes .userName {
  line-height: 0.8rem;
}
.orderDetail .orderMes .userName .phone {
  float: right;
}
.orderDetail .orderMes .address {
  font-size: 0.24rem;
  color: #999;
}
.orderDetail .goodsList h3 {
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.3rem;
  font-weight: normal;
  color: #31bfcf;
  padding-left: 0.3rem;
}
.orderDetail .goodsList h3 .fa-angle-right {
  color: #ccc;
  font-size: 0.5rem;
  vertical-align: middle;
}
.orderDetail .goodsList li {
  border-bottom: 2px solid #fff;
  height: 1rem;
  line-height: 1rem;
  position: relative;
  padding-left: 1.3rem;
  background-color: #f5f5f5;
}
.orderDetail .goodsList li .goodsName {
  font-size: 0.4rem;
  position: absolute;
  height: 1rem;
  line-height: 1rem;
  width: 1rem;
  top: 0;
  left: 0.3rem;
}
.orderDetail .goodsList li .goodsType,
.orderDetail .goodsList li .goodsColor {
  color: #999;
  font-size: 0.24rem;
}
.orderDetail .goodsList li .goodsNum {
  position: absolute;
  right: 0.2rem;
}
.orderDetail .goodsList li .goodsPrice {
  color: #f00;
  right: 1.3rem;
  position: absolute;
}
.orderDetail .detailFooter {
  position: fixed;
  width: 100%;
  height: 0.8rem;
  background-color: #31bfcf;
  bottom: 0;
  color: #fff;
  line-height: 0.8rem;
  box-sizing: border-box;
  padding: 0 0.3rem;
  text-align: right;
  font-size: 0.24rem;
}
.orderDetail .detailFooter .totalMoney {
  font-size: 0.3rem;
}
.orderDetail .toolBtn {
  position: fixed;
  bottom: 0.9rem;
  right: 0.3rem;
}
.orderDetail .toolBtn li {
  width: 1rem;
  height: 1rem;
  color: #fff;
  margin-bottom: 0.2rem;
  background-color: #31bfcf;
  border-radius: 50%;
  font-size: 0.24rem;
  text-align: center;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  line-height: 0.3rem;
}
.orderDetail .toolBtn .deleteOrder {
  background-color: #cd270a;
}



