
.customerAdd {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 0.8rem;
  background-color: #eee;
}
.customerAdd .form {
  background-color: #fff;
  padding: 0 0.3rem;
}
.customerAdd .form .formRow {
  border-bottom: 1px solid #eee;
  position: relative;
  line-height: 1rem;
  padding-left: 1.5rem;
}
.customerAdd .form .formRow span {
  position: absolute;
  top: 0;
  left: 0;
}
.customerAdd .form .formRow b {
  color: #f00;
  line-height: 1rem;
}
.customerAdd .form .formRow input,
.customerAdd .form .formRow select {
  background: none;
  border: none;
  font-size: 0.3rem;
  color: #999;
  display: block;
  width: 100%;
  height: 1rem;
  outline: none;
}
.customerAdd .form .formRow .error {
  color: #f00;
  font-size: 0.2rem;
  line-height: 0.3rem;
}
.customerAdd .form .formRow:last-child {
  border: none;
}
.customerAdd .tip {
  padding: 0 0.3rem;
  font-size: 0.24rem;
  color: #999;
}
.customerAdd .btn {
  width: 90%;
  display: block;
  margin: 0.5rem auto;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.2rem;
}
.customerAdd .save {
  color: #fff;
  background-color: #31bfcf;
}
.customerAdd .cancel {
  background-color: #fff;
}



