
.loading {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 9999;
}
.loading .loadingTip {
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  top: 50%;
  left: 50%;
  margin: -0.8rem 0 0 -0.8rem;
}
.loading img {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-animation: loading 2s linear infinite;
          animation: loading 2s linear infinite;
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



