  height: 0.8rem;
  line-height: 0.5rem;
  color: #fff;
  font-size: 0.24rem;
}
.order .searchRow .screenBtn {
  position: absolute;
  color: #fff;
  height: 0.8rem;
  line-height: 0.8rem;
  top: 0;
  right: 0;
  text-align: center;
  width: 1.5rem;
  font-size: 0.3rem;
}
.order .filter {
  position: absolute;
  width: 100%;
  top: 0.8rem;
  bottom: 0.9rem;
  background-color: #fff;
  z-index: 10;
}
.order .filter .row {
  height: 0.7rem;
  line-height: 0.7rem;
  text-align: center;
  padding: 0.1rem 0.3rem;
}
.order .filter .dataRow1 {
  height: 0.7rem;
  padding: 0.1rem 0.3rem;
}
.order .filter .dataRow1 .cov-vue-date {
  display: block;
}
.order .filter .dataRow1 .cov-datepicker {
  box-sizing: border-box;
  background: none;
  border: 1px solid #eee;
  height: 0.7rem;
  border-radius: 0.2rem;
  text-align: center;
  display: block;
  width: 100%;
}
.order .filter .dataRow2 {
  height: 0.7rem;
  padding: 0.1rem 0.3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.order .filter .dataRow2 .line {
  line-height: 0.7rem;
}
.order .filter .dataRow2 .cov-datepicker {
  box-sizing: border-box;
  background: none;
  border: 1px solid #eee;
  height: 0.7rem;
  border-radius: 0.2rem;
  text-align: center;
  display: block;
  width: 3.2rem;
}
.order .filter .active {
  color: #FFB97C;
}
.order .filter .active .cov-datepicker {
  border: 1px solid #FFB97C;
  color: #FFB97C;
}
.order .filter .active .cov-datepicker::-webkit-input-placeholder {
  color: #FFB97C;
}
.order .filter .active .cov-datepicker:-ms-input-placeholder {
  color: #FFB97C;
}
.order .filter .active .cov-datepicker::placeholder {
  color: #FFB97C;
}
.order .filter .closeFilter {
  margin: 0.5rem 0.3rem 0 0.3rem;
  height: 0.9rem;
  display: block;
  background-color: #31bfcf;
  color: #fff;
  border-radius: 0.2rem;
  text-align: center;
  line-height: 0.9rem;
}
.order .listCon {
  position: absolute;
