}
.order .list .customerMes {
  height: 0.7rem;
  line-height: 0.7rem;
}
.order .list .customerMes .phone {
  float: right;
  font-size: 0.24rem;
}
.order .list .deliveryAddress {
  font-size: 0.24rem;
  color: #999;
}
.order .list .money {
  float: right;
  color: #f00;
}



