
.vDialog {
  position: fixed;
  top: 40%;
  left: 50%;
  width: 4rem;
  background-color: #fff;
  border-radius: 0.2rem;
  overflow: hidden;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 4px 4px 15px #999;
  z-index: 9999;
}
.vDialog .dialogTitle {
  font-size: 0.3rem;
  line-height: 0.8rem;
  text-align: center;
  color: #60a0ff;
}
.vDialog .dialogCon {
  font-size: 0.28rem;
  color: #555;
  padding: 0.3rem;
  line-height: 0.4rem;
  text-align: center;
  border-bottom: 1px solid #ccc;
  word-break: break-all;
  word-wrap: break-word;
  position: relative;
}
.vDialog .dialogBtn {
  height: 0.8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.vDialog .dialogBtn a {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  text-align: center;
  line-height: 0.8rem;
}
.vDialog .dialogBtn .btnConfirm {
  color: #60a0ff;
}



