
.customerDetail {
  padding: 0.8rem 0 3.5rem 0;
  background-color: #f5f5f5;
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: auto;
}
.customerDetail .customerMes {
  padding: 0.2rem 0.3rem;
  background-color: #fff;
  margin-bottom: 0.2rem;
}
.customerDetail .customerMes .username {
  line-height: 0.6rem;
  border-bottom: 1px solid #eee;
  margin-bottom: 0.1rem;
}
.customerDetail .customerMes .phone {
  float: right;
}
.customerDetail .customerMes .itemRow {
  line-height: 0.5rem;
  font-size: 0.26rem;
  color: #999;
}
.customerDetail .orderList h3 {
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.3rem;
  font-weight: normal;
  color: #31bfcf;
  padding-left: 0.3rem;
  border-bottom: 1px solid #eee;
  background-color: #fff;
}
.customerDetail .orderList h3 .fa-angle-right {
  color: #ccc;
  font-size: 0.5rem;
  vertical-align: middle;
}
.customerDetail .orderList li {
  background-color: #fff;
  padding: 0.2rem 0.3rem;
  margin-bottom: 0.2rem;
}
.customerDetail .orderList li a {
  display: block;
}
.customerDetail .orderList li .orderTime {
  font-size: 0.24rem;
  border-bottom: 1px solid #eee;
  color: #999;
  line-height: 0.5rem;
  margin-bottom: 0.2rem;
}
.customerDetail .orderList li .deliveryState {
  float: right;
  color: #f00;
}
.customerDetail .orderList li .deliveryAddress {
  font-size: 0.24rem;
  color: #999;
}
.customerDetail .orderList li .money {
  float: right;
  color: #999;
}
.customerDetail .toolBtn {
  position: fixed;
  bottom: 0.9rem;
  right: 0.3rem;
}
.customerDetail .toolBtn li {
  width: 1rem;
  height: 1rem;
  color: #fff;
  margin-bottom: 0.2rem;
  background-color: #31bfcf;
  border-radius: 50%;
  font-size: 0.24rem;
  text-align: center;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  line-height: 0.3rem;
}
.customerDetail .toolBtn .deleteOrder {
  background-color: #cd270a;
}



