
.customer {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0.9rem;
  overflow: auto;
  padding-top: 0.8rem;
}
.customer .searchRow {
  padding: 0.15rem 0.3rem;
  background-color: #31bfcf;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  box-sizing: border-box;
}
.customer .searchRow input {
  display: block;
  border: 1px solid #fff;
  background: none;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  height: 0.5rem;
  border-radius: 0.3rem;
  padding-left: 0.6rem;
}
.customer .searchRow input::-webkit-input-placeholder {
  color: #fff;
}
.customer .searchRow input:-ms-input-placeholder {
  color: #fff;
}
.customer .searchRow input::placeholder {
  color: #fff;
}
.customer .searchRow .fa-search {
  color: #fff;
  position: absolute;
  top: 0.26rem;
  left: 0.48rem;
}
.customer .customerList {
  padding: 0 0.2rem;
}
.customer .customerList li {
  border-bottom: 1px solid #eee;
  padding: 0.2rem 0 0.1rem 0;
}
.customer .customerList a {
  display: block;
}
.customer .customerList .nameRow {
  line-height: 0.4rem;
  font-size: 0.3rem;
}
.customer .customerList .nameRow .phone {
  float: right;
  font-size: 0.24rem;
}
.customer .customerList .address {
  color: #999;
  font-size: 0.24rem;
  line-height: 0.5rem;
}
.customer .toolBtn {
  position: absolute;
  bottom: 0.3rem;
  right: 0.3rem;
}
.customer .toolBtn li {
  width: 1rem;
  height: 1rem;
  color: #fff;
  margin-bottom: 0.2rem;
  background-color: #31bfcf;
  border-radius: 50%;
  font-size: 0.24rem;
  text-align: center;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  line-height: 0.3rem;
}



